.tags-input-wrapper .react-tagsinput-input{
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  min-width:100px;
  flex:1 1;
  margin:0;
  padding:0;
  font-size:14px;
  line-height:1;
  color:#0d0229;
  font-family:"SF-Pro-Text",sans-serif
}

.tags-input-wrapper .react-tagsinput-input::-webkit-input-placeholder{
  color:#8a8a8a
}

.tags-input-wrapper .react-tagsinput-input:-ms-input-placeholder{
  color:#8a8a8a
}

.tags-input-wrapper .react-tagsinput-input::placeholder{
  color:#8a8a8a
}

.text-validator-wrapper .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline{
  border:1px solid #4f0c71
}

.text-validator-wrapper .MuiInputBase-input{
  font-size:14px;
  color:#8a8a8a
}

.text-validator-wrapper .MuiFormHelperText-root.Mui-error{
  font-size:13px
}

.text-validator-wrapper fieldset{
  border:1px solid #e0e0e0;
  border-radius:0px
}

.text-validator-wrapper fieldset legend{
  width:0px
}

.MuiFormHelperText-root.Mui-error{
  font-size:16px;
  margin:10px 0 0 0;
  line-height:1
}

.MuiInputBase-formControl{
  font-size:14px;
  font-weight:400;
  font-family:"SF-Pro-Text",sans-serif;
  color:red
}

.select-custom .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline{
  border:1px solid #4f0c71
}

.select-custom .MuiInputBase-input{
  font-size:14px;
  color:#8a8a8a
}

.select-custom fieldset{
  border:1px solid #e0e0e0 #e9e9e9;
  border-radius:0px
}

.select-custom fieldset legend{
  width:0px
}

.date-picker-wrapper .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline{
  border:1px solid #4f0c71
}

.date-picker-wrapper .MuiInputBase-input{
  font-size:14px;
  color:#8a8a8a
}

.date-picker-wrapper fieldset{
  border:1px solid #e0e0e0;
  border-radius:0px
}

.date-picker-wrapper fieldset legend{
  width:0px
}

.tags-input-wrapper div[class$="-control"]{
  border:1px solid #e9e9e9;
  border-radius:0px;
  box-shadow:none
}

.tags-input-wrapper div[class$="-control"]:hover{
  border-color:#4f0c71
}

.tags-input-wrapper div[class$="-control"]:focus{
  border-color:#4f0c71
}

.tags-input-wrapper div[class$="-control"] div[class$="-multiValue"]{
  background-color:#c05299;
  border-radius:0px
}

.MuiButton-root:hover{
  background-color:#4f0c71
}

